import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-menu',
  templateUrl: './menu.component.html',
  styleUrls: ['./menu.component.css']
})
export class MenuComponent implements OnInit {
  Menu: boolean;
  Slowka: boolean;
  filmMenu: boolean;

  constructor() {
  this.Menu = true;
  this.Slowka = false;
  this.filmMenu = false;
 }

  ngOnInit() {
  }
  pokazMenu(){
    this.Menu = true;
    this.Slowka = false;
    this.filmMenu = false;
  }

  podakazSlowka(): void{
    this.Slowka= true;
    this.Menu= false;
    this.filmMenu = false;
  }

  pokazYoutube(){
    this.Slowka= false;
    this.Menu= false;
    this.filmMenu = true;
  }


}
